import GQL from '../helpers/GQL'


const User = {
    listDokter: (clientID, token, kodeklinik) => {
        return new Promise((resolve, reject) => {
            const query = `
                query {
                    listUser(jenisUser:"dokter", kodeklinik:"${kodeklinik}"){
                        username
                        nama
                        noHp
                      }
                }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.listUser) return (resolve(resp.data.listUser));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
    simpanDokter: (clientID, token, kodeklinik, namaklinik, username, nama, noHp, password) => {
        return new Promise((resolve, reject) => {
            const query = `
                mutation {
                    simpanUser(input:{username:"${username}", password:"${password}", nama:"${nama}", noHp:"${noHp}", jenisUser:"dokter", validasi: true},kodeklinik: "${kodeklinik}", namaklinik: "${namaklinik}")
                }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.simpanUser) return (resolve(resp.data.simpanUser));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
    detailDokter: (clientID, token, username) => {
        return new Promise((resolve, reject) => {
            const query = `
                query {
                    detailUser(jenisUser:"dokter", username:"${username}"){
                        jadwal{
                            hari
                            jamMulai
                            jamSelesai
                        }
                      }
                }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.detailUser.jadwal) return (resolve(resp.data.detailUser.jadwal));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
    simpanJadwalDokter: (clientID, token, username, hari, jamMulai, jamSelesai) => {
        return new Promise((resolve, reject) => {
            const query = `
                mutation {
                    simpanJadwalDokter(username: "${username}",input:{hari: "${hari}",jamMulai:"${jamMulai}", jamSelesai:"${jamSelesai}"})
                }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.simpanJadwalDokter) return (resolve(resp.data.simpanJadwalDokter));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
    hapusJadwalDokter: (clientID, token, username, hari) => {
        return new Promise((resolve, reject) => {
            const query = `
                mutation {
                    hapusJadwalDokter(username: "${username}",hari:"${hari}")
                }
            `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.hapusJadwalDokter) return (resolve(resp.data.hapusJadwalDokter));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
};

export default User;
