import React, { useState, useEffect } from 'react'
import { Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Button, Table } from "reactstrap";
import mDokter from '../../../../Controler/c_Dokter'
import validate from 'validate.js'
import Notifikasi from '../../../../helpers/Notifikasi'
import { isArray } from "util";
import SettingsIcon from '@material-ui/icons/Settings';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import Tooltip from '@material-ui/core/Tooltip';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import ButtonMI from '@material-ui/core/Button';
import CancelIcon from '@material-ui/icons/Cancel';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import hari from '../../../../data/hari'
import { MuiPickersUtilsProvider, TimePicker } from "@material-ui/pickers";
import DateFnsUtils from '@date-io/date-fns';
import moment from 'moment'
import Confirm from '../../../../helpers/Confirm'

const ModalDokter = (props) => {
    const clientID = props.clientID
    const token = props.token
    const detail = props.detailUser || {}
    const [jadwal, setJadwal] = useState([])
    const [isList, setIsList] = useState(true)
    const [isReload, setIsReload] = useState(true)
    const [isConfirm, setIsConfirm] = useState(false)
    const [textConfirm, setTextConfirm] = useState("")
    const [detailJadwal, setDetailJadwal] = useState({})

    const [form, setForm] = useState({
        hari: '',
        jamMulai: moment().set({ hour: 0, minute: 0, second: 0 }),
        jamSelesai: moment().set({ hour: 0, minute: 0, second: 0 }),
    })
    useEffect(() => {
        if (detail.username) {
            mDokter.detailDokter(clientID, token, detail.username).then(resp => {
                setJadwal(resp);
            }).catch(err => {
                console.error(err);
            }).finally(() => setIsReload(false))
        }
        return () => {

        }
    }, [clientID, token, detail, isReload])
    const handleClose = () => {
        props.setOpen(false)
    }
    const handleChange = (ev) => {
        var value = ev.target.value;
        const key = ev.target.id || ev.target.name;
        var newForm = { ...form }
        newForm[key] = value;
        setForm(newForm);
    }
    const validateForm = () => {
        const { hari } = form;
        const promForm = new Promise((resolve, reject) => {
            const constraint = {
                hari: {
                    length: {
                        minimum: 1,
                        message: "Hari wajib isi"
                    },
                }
            }
            const validator = validate({ hari }, constraint, { fullMessages: false });
            if (validator === undefined) {
                resolve();
            } else {
                reject(validator);
            }
        })
        return Promise.all([promForm]);
    }
    const handleSimpan = () => {
        validateForm().then(() => {
            const { hari, jamMulai, jamSelesai } = form;
            mDokter.simpanJadwalDokter(clientID, token, detail.username, hari, moment(jamMulai).format("HH:mm"), moment(jamSelesai).format("HH:mm")).then(resp => {
                if (resp) {
                    Notifikasi("success", "Berhasil disimpan", "")
                    setIsReload(true)
                    setIsList(true)
                } else {
                    Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
                }
            }).catch(err => {
                if (isArray(err) && err.length > 0) {
                    Notifikasi("error", err[0].message, "Gagal Simpan")
                } else if (typeof err) {
                    Notifikasi("error", err.message, "Gagal Simpan")
                } else {
                    console.log(err);
                }
            })
        }).catch(err => {
            const keys = Object.keys(err);
            for (const key of keys) {
                Notifikasi("warning", err[key])
            }
        })
    }
    const add = () => {
        setIsList(false)
        setForm({
            hari: '',
            jamMulai: moment().set({ hour: 0, minute: 0, second: 0 }),
            jamSelesai: moment().set({ hour: 0, minute: 0, second: 0 }),
        })
    }
    const edit = (data) => {
        console.log(data)
        setIsList(false)
        setForm({
            hari: data.hari,
            jamMulai: moment().set({ hour: data.jamMulai.substring(0, 2), minute: data.jamMulai.substring(3, 5), second: 0 }),
            jamSelesai: moment().set({ hour: data.jamSelesai.substring(0, 2), minute: data.jamSelesai.substring(3, 5), second: 0 }),
        })
    }
    const confirmHapus = (data) => {
        setDetailJadwal(data)
        setTextConfirm(`Yakin ingin hapus jadwal ${data.hari}? Data tidak dapat dikembalikan setelah hapus`)
        setIsConfirm(true)
    }
    const hapus = () => {
        mDokter.hapusJadwalDokter(clientID, token, detail.username, detailJadwal.hari).then(resp => {
            if (resp) {
                Notifikasi("success", `jadwal ${detailJadwal.hari} berhasil dihapus`, "")
                setIsReload(true)
                setIsList(true)
                setIsConfirm(false)
            } else {
                Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
            }
        }).catch(err => {
            if (isArray(err) && err.length > 0) {
                Notifikasi("error", err[0].message, "Gagal Simpan")
            } else if (typeof err) {
                Notifikasi("error", err.message, "Gagal Simpan")
            } else {
                console.log(err);
            }
        })
    }
    return (
        <Modal
            isOpen={props.open}
            toggle={handleClose}
            wrapClassName="modal-right"
        >
            <ModalHeader toggle={handleClose}>Jadwal Dokter<br /><b>{detail.nama}</b></ModalHeader>
            <ModalBody>
                {isList ? (
                    <Row>
                        <Col>

                            <ButtonMI
                                onClick={add}
                                size="small"
                                variant="contained"
                                color="primary"
                                startIcon={<AddCircleIcon />}
                            >Add Jadwal</ButtonMI>
                            <Table responsive size="sm" hover>
                                <thead>
                                    <tr>
                                        <th>Hari</th>
                                        <th>Jam Mulai</th>
                                        <th>Jam Selesai</th>
                                        <th className="text-center"><SettingsIcon /></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {jadwal.map((el, i) => {
                                        return (
                                            <tr key={i}>
                                                <th>{el.hari}</th>
                                                <td>{el.jamMulai}</td>
                                                <td>{el.jamSelesai}</td>
                                                <td width="60">
                                                    <Tooltip onClick={edit.bind(this, el)} className="klik" title="edit"><EditIcon /></Tooltip>
                                                    <Tooltip onClick={confirmHapus.bind(this, el)} className="klik" title="delete"><DeleteIcon /></Tooltip>
                                                </td>
                                            </tr>
                                        )
                                    })}
                                </tbody>
                            </Table>
                        </Col>
                    </Row>
                ) : (
                        <Row>
                            <Col>
                                <ButtonMI
                                    onClick={() => setIsList(true)}
                                    size="small"
                                    variant="contained"
                                    color="secondary"
                                    startIcon={<CancelIcon />}
                                >Close Form</ButtonMI>
                            </Col>
                            <Col xs={12} md={12} className="marginFrom">
                                <FormControl fullWidth >
                                    <InputLabel id="hari">Hari</InputLabel>
                                    <Select
                                        labelId="hari"
                                        id="hari"
                                        name="hari"
                                        value={form.hari}
                                        onChange={handleChange}
                                    >
                                        {hari.map((el, i) => (
                                            <MenuItem key={i} value={el}>{el}</MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </Col>
                            <Col xs={12} md={12} className="marginFrom">
                                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                    <TimePicker
                                        fullWidth={true}
                                        margin="normal"
                                        id="jamMulai"
                                        label="Jam Mulai"
                                        value={form.jamMulai}
                                        onChange={(d) => setForm({ ...form, jamMulai: d })}
                                        clearable
                                        ampm={false}
                                        minutesStep={30}
                                    />
                                </MuiPickersUtilsProvider>
                            </Col>
                            <Col xs={12} md={12} className="marginFrom">
                                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                    <TimePicker
                                        fullWidth={true}
                                        margin="normal"
                                        id="jamSelesai"
                                        label="Jam Selesai"
                                        value={form.jamSelesai}
                                        onChange={(d) => setForm({ ...form, jamSelesai: d })}
                                        clearable
                                        ampm={false}
                                        minutesStep={30}
                                    />
                                </MuiPickersUtilsProvider>
                            </Col>
                        </Row>
                    )}


            </ModalBody>
            <ModalFooter>
                <Button color="secondary" onClick={handleClose}>Cancel</Button>
                <Button color="primary" onClick={handleSimpan}>Save Jadwal</Button>{" "}
            </ModalFooter>

            {isConfirm && (
                <Confirm open={isConfirm} setOpen={setIsConfirm} textConfirm={textConfirm} oke={hapus} />
            )}
        </Modal>
    )
}

export default ModalDokter;